export class ExpenseSummaryReportViewController extends ReportBaseViewController {
	constructor( options = {} ) {
		_.defaults( options, {} );

		super( options );
	}

	initReport( options ) {
		this.script_name = 'UserExpenseReport';
		this.viewId = 'ExpenseSummaryReport';
		this.context_menu_name = $.i18n._( 'Expense Summary' );
		this.navigation_label = $.i18n._( 'Saved Report' );
		this.view_file = 'ExpenseSummaryReportView.html';
		this.api = TTAPI.APIUserExpenseReport;
	}

	getCustomContextMenuModel() {
		return { include: ['default'] }; // TODO refactor to remove the need for this once all reports done.
	}

}